@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.icon {
    display: flex;
    justify-content: center;
    transition: 0.3s ease-in-out all;
    fill: var(--text_color);
}

.icon svg {
    width: 30px;
}
.base_button {
    cursor: pointer;
    margin: 20px;
    height: 75px;
    width: 75px;
    font-weight: bold;
    border-radius: 205px;
    background: linear-gradient(145deg, #ffffff, #e6e6e6);
    box-shadow: 8px 8px 15px #a8a8a8, -8px -8px 15px #ffffff;
    outline: none;
    border: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-tap-highlight-color: transparent;
    transition: 0.3s ease-in-out box-shadow;
    border: solid 1px var(--border);
    box-shadow: 8px 8px 15px var(--right_shadow), -8px -8px 15px var(--left_shadow);
    background: linear-gradient(145deg, var(--right_gradient), var(--left_gradient));
}

.base_button:focus {
    box-shadow: 4px 4px 8px var(--right_shadow), -4px -4px 8px var(--left_shadow);
}

.base_button:disabled {
    cursor: auto;
    cursor: initial;
    box-shadow: none;
}

.base_button:disabled .icon svg {
    fill: var(--paused-color);
}
.timer {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.timer__header {
    font-size: 32px;
}

.timer__time {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 40px;
    height: 300px;
    width: 300px;
    padding: 10px;
    border-radius: 100%;
    position: relative;
    transition: 0.3s ease-in-out color;
    border: solid 1px var(--border);
    box-shadow: 8px 8px 15px var(--right_shadow), -8px -8px 15px var(--left_shadow), inset 3px 3px 5px var(--left_inside_shadow);
    background: linear-gradient(145deg, var(--right_gradient), var(--left_gradient));
}

.timer__time::after {
    content: "";
    position: absolute;
    height: 75%;
    width: 75%;
    background-color: transparent;
    border-radius: 50%;
    border: solid 1px var(--border);
    box-shadow: 5px 5px 10px var(--right_shadow);
}

.timer__time_paused {
    color: var(--paused-color);
}

.timer__controller {
    display: flex;
    align-items: center;
    justify-content: center;
}

.timer__button {
    cursor: pointer;
    margin: 30px;
    height: 75px;
    width: 75px;
    font-weight: bold;
    border-radius: 205px;
    background: linear-gradient(145deg, #ffffff, #e6e6e6);
    box-shadow: 8px 8px 15px #a8a8a8, -8px -8px 15px #ffffff;
    outline: none;
    border: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-tap-highlight-color: transparent;
    transition: 0.3s ease-in-out box-shadow;
}

.timer__button:hover {
    box-shadow: 4px 4px 8px #a8a8a8, -4px -4px 8px #ffffff;
}

.timer__button:focus {
    box-shadow: 2px 2px 4px #a8a8a8, -2px -2px 4px #ffffff;
}

.timer__button:disabled {
    cursor: auto;
    cursor: initial;
    box-shadow: none;
}

.timer__button:disabled .icon svg {
    fill: var(--paused-color);
}

.is_theme_changing * {
    transition: none;
}
.App {
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: var(--background);
	color: var(--text_color);
	fill: var(--text_color);
}
html, body, #root {
  height: 100%;
}

* {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
:root {
    --background: rgb(220, 220, 220);
    --text_color: rgb(38, 39, 44);
    --left_gradient: rgb(230, 230, 230);
    --right_gradient: rgb(255, 255, 255);
    --border: solid 1px rgb(215, 215, 215);

    --left_shadow: rgb(255, 255, 255);
    --right_shadow: rgba(94, 104, 121, 0.671);
    --left_inside_shadow: rgba(94, 104, 121, 0.671);
    --right_inside_shadow: rgb(230, 230, 230);

    --default-color: rgb(30, 30, 30);
    --paused-color: rgb(100, 100, 100);
}

[data-theme="dark"] {
    --background: rgb(38, 39, 44);
    --text_color: rgb(225, 225, 225);
    --left_gradient: rgb(32, 31, 36);
    --right_gradient: rgb(33, 34, 39);
    --border: rgb(43, 44, 49);

    --left_shadow: rgb(50, 50, 50);
    --right_shadow: rgb(25, 25, 25);
    --left_inside_shadow: rgb(12, 13, 15);
    --right_inside_shadow: rgb(36, 37, 42);
}
